import http from '@/http/interface.js'
export default {
	data() {
		return {
			rememberPass : true,
			userPhone : '',
			userPwd : '',
		};
	},
	onLoad(options) {
		if(options.msg!=undefined){
			uni.showToast({
				icon:'none',
				position: 'bottom',
				title: options.msg
			})
		}
	},
	methods:{
		goLogin(){
			if(this.userPhone == '' || this.userPwd == ''){
				uni.showToast({
					title: '用户名或密码不能为空！',
					position: 'bottom',
					icon: 'none'
				})
				return;
			}
			uni.showLoading({
				title: '正在登录~',
				mask:true
			});
			http.get('member/login',{phone:this.userPhone,loginPwd:this.userPwd}).then((res)=>{
				if(res.data.success){
					let obj = res.data.data;
					if(res.data.data.capitalType == undefined){
						obj.capitalType = '';
					}
					obj.phone = this.userPhone;
					this.$store.commit('userIsLogin',obj);
					uni.redirectTo({
						url : '/pages/main/main'
					})
				}
			})
		}
	}
}
